import React, {memo} from 'react'
import Typography from '@material-ui/core/Typography'
import Breadcrumbs from '@material-ui/core/Breadcrumbs'
import Link from '@material-ui/core/Link'
import {useFiles, useSelector} from "../../store";
import {selectors as sels} from "../../reducer";

function Breadcumb() {
    const [, dispatch] = useFiles()
    const { breadcumb} = useSelector(sels.makeCurrent)

    const onClick = (e, id) => dispatch('access', id)

    return (
        <div className="h-full flex items-center pl-2">
            <Breadcrumbs aria-label="breadcrumb">
                {breadcumb.map((node, k) => {
                    if(k === (breadcumb.length-1)) return <Typography key={k} color="textPrimary">{node.model.name}</Typography>

                    return <Link key={k} color="inherit" onClick={e => onClick(e, node.model.id)}>{node.model.name}</Link>
                })}
            </Breadcrumbs>
        </div>

    )
}

export default memo(Breadcumb)