import React, {memo} from 'react'
import {useFiles, useSelector} from '../../store'
import {selectors as sels} from '../../reducer'

function Main({data}) {
    const [state, dispatch] = useFiles()
    const {files} = useSelector(sels.makeCurrent)

    const onDbClick = (e, v) => dispatch('access', v.id)
    const onClick = (e, v) => dispatch('selected', v.id)

    return (
        <div>
            <table className="table">
                <tbody>
                {files.map((v, k) => (
                    <tr key={k} onDoubleClick={e => onDbClick(e, v)} onClick={e => onClick(e, v)}>
                        <td>{v.name}</td>
                    </tr>
                ))}
                </tbody>
            </table>

        </div>
    )
}

export default memo(Main)