import React, {memo} from 'react'
import {ProgressBar} from 'react-bootstrap'

function Info() {
    return (
        <div className="p-2">
            <div className="font-medium">Storage</div>
            <div className="mt-2 mb-2`">
                <ProgressBar now={60} style={{height: 5}}/>
            </div>
            <div>
                5.4GB of 17GB used
            </div>
        </div>
    )
}

export default memo(Info)