import React, {memo, useEffect} from 'react'
import {Layout as LAYOUT} from "@ttungbmt/react.layout"
import {Sidebar, Info, Breadcumb, Detail, RightTool, Main} from './partials'
import {useQuery} from 'react-query'
import axios from 'axios'
import styled from '@emotion/styled'
import FilesStore, {useDispatch} from './store'

const Wrapper = styled.div`
    .slot-sidebar {height: 100%; width: 100%; border-right: 1px solid #edf2f7;}
    .slot-actions, .slot-breadcrumb {border-bottom: 1px solid #dadce0;}
    .slot-actions {border-right: 1px solid #dadce0;}
    .slot-info {border-top: 1px solid #edf2f7;}
    .slot-detail {border-left: 1px solid #edf2f7;}
`

const fetchFiles = async () => axios.get(`/api/geo-file/drive/1/files`).then(({data}) => data)


function FileManager({children}) {
    return (
        <FilesStore.Provider>
            <Wrapper>
                <App />
            </Wrapper>
        </FilesStore.Provider>
    )
}

function initApp($a) {
    return (dispatch, getState) => {
        try {
            // const state = getState()
            console.log(getState);
        } catch (e) {
            console.log(e);
        }

        // console.log($a, dispatch)
    }
}


function App() {
    const dispatch = useDispatch()
    const {status, data, error, isFetching: loading, refetch} = useQuery('files', fetchFiles, {manual: true})
    const Layout = LAYOUT.FileManager

    useEffect(() => {
        refetch()
    }, [])

    useEffect(() => {
        if (!loading && data) {
            dispatch('init', data)
            setTimeout(() => dispatch(initApp('123')), 1000)

        }
    }, [loading])

    return (
        <Layout>
            <div slot="actions" className="border-solid border-gray-600 border-b-2">
                Actions
            </div>
            <div slot="sidebar-content">
                <Sidebar/>
            </div>
            <div slot="info">
                <Info/>
            </div>
            <div slot="breadcrumb">
                <div className="flex justify-between">
                    <div><Breadcumb/></div>
                    <div><RightTool/></div>
                </div>
            </div>
            <div slot="main">
                {data && <Main data={data}/>}
            </div>
            <div slot="detail">
                <Detail/>
            </div>
        </Layout>

    );
}

export default memo(FileManager)
