import React, {memo} from 'react'
import {get, isEmpty} from 'lodash-es'
import {selectors as sels} from '../../reducer'
import {useSelector} from '../../store'

function Detail() {
    const {detail: d} = useSelector(sels.makeCurrent)

    if(isEmpty(d)) return <div>Not Found</div>

    return (
        <div>
            <div className="h-16 bg-red-400 flex items-center">
                <div className="flex justify-between items-center w-100 pl-3 pr-3">
                    <div className="font-bold text-xl">
                        {d.name}
                    </div>
                    <div>X</div>
                </div>
            </div>
            <table className="w-full m-2">
                <tbody>
                <tr >
                    <td style={{width: 100}}>Type</td>
                    <td>1</td>
                </tr>
                <tr >
                    <td>Size</td>
                    <td>{d.size} MB</td>
                </tr>
                <tr >
                    <td>Created</td>
                    <td>3</td>
                </tr>
                </tbody>
            </table>
        </div>
    )
}

export default memo(Detail)