const actionsMap = {
    ['init']: (state, payload) => {
        state.files = payload.files
        state.drive = payload.drive
        state.rootIds = payload.rootIds
        state.tree = treeModel([payload.drive, ...payload.files], 'flat')
    },
    ['selected']: (state, payload) => {
        state.selected_id = payload
    },
    ['access']: (state, payload) => {
        state.selected_id = payload
        if(state.tree.first(payload).hasChildren()) state.access_id = payload
    },
}

