import React, {memo} from 'react'
import { InfoOutlined, ListSharp } from '@material-ui/icons'
import IconButton from '@material-ui/core/IconButton'

import styled from '@emotion/styled'

const Wrapper = styled.div`
  .MuiIconButton-sizeSmall { padding: 7px; }
`

function RightTool() {
    return (
        <Wrapper className="flex pr-2">
            <IconButton size={'small'}><ListSharp/></IconButton>
            <IconButton size={'small'}><InfoOutlined /></IconButton>
        </Wrapper>
    )
}

export default memo(RightTool)