import React, {useCallback} from 'react'
import {useImmerReducer, useImmer} from 'use-immer'
import {isString, isPlainObject, isFunction} from 'lodash-es'
import constate from 'constate'

export default function createStore(initialState, reducer = null) {
    const [Provider, useStore] = constate(() => {
        const [state, dispatch] = reducer ? useImmerReducer(reducer, initialState) : useImmer(initialState);

        // const customDispatch = useCallback((...args) => {
        //     let [arg1, arg2] = args
        //     if (isString(arg1)) dispatch({type: arg1, payload: arg2})
        //     if (isPlainObject(arg1) && arg1.type) dispatch(arg1)
        //     if (isFunction(arg1)) arg1(dispatch, state)
        // }, [])

        const customDispatch = (...args) => {
            let [arg1, arg2] = args
            if (isString(arg1)) dispatch({type: arg1, payload: arg2})
            if (isPlainObject(arg1) && arg1.type) dispatch(arg1)
            if (isFunction(arg1)) arg1(dispatch)
        }

        return Object.assign([state, customDispatch], {state, dispatch: customDispatch});
    })



    function useSelector(selector) {
        const [state] = useStore()
        return selector(state)
    }

    function useDispatch() {
        const [, dispatch] = useStore()
        return dispatch
    }

    function useContext() {
        const [state] = useStore()

        return state
    }

    return {
        Provider,
        useStore,
        useSelector,
        useDispatch,
        useContext
    }

}


