import {find, includes, isArray, isEqual} from 'lodash-es'
import {createSelector} from 'reselect'

function filterIn(collection, ids, key = 'id') {
    return collection.filter(f => isArray(ids) ? includes(ids, f[key]) : isEqual(ids, f[key]))
}

export const makeCurrent = createSelector(
    state => state.drive,
    state => state.files,
    state => state.rootIds,
    state => state.selected_id,
    state => state.access_id,
    state => state.tree,
    (drive, files, rootIds, selected_id, access_id, tree) => {

        return {
            files: access_id ? filterIn(files, [access_id], 'parent_id') : filterIn(files, rootIds),
            breadcumb: tree ? (access_id ? tree.first(access_id).getPath() : [tree.node]) : [],
            detail: (selected_id && selected_id !== drive.id) ? find(files, {id: selected_id}) : drive
        }
    }
)
